fetch("./data.json")
  .then(function (response) {
    return response.json();
  })
  .then(function (data) {
    appendData(data);
  })
  .catch(function (err) {
    console.log("error: " + err);
  });

let arr = [];

function removeItem(arr, val) {
  let index = arr.findIndex((item) => item === val);
  if (index !== -1) {
    arr.splice(index, 1);
    //console.log(arr);
    return arr;
  }
}

function appendData(data) {
  let app = document.getElementById("app");

  for (let i in data) {
    // console.log(data[i].name);
    let itemCategory = document.createElement("div");
    itemCategory.classList.add("itemCategory");
    let heading = document.createElement("h5");
    heading.innerHTML = data[i].name;
    itemCategory.appendChild(heading);
    // itemCategory.innerHTML = data[i].name;

    for (let j in data[i].productList) {
      // console.log(data[i].productList[j].name);
      // console.log(data[i].productList[j].price);

      let item = document.createElement("div");
      item.classList.add("item");

      let name = document.createElement("p");
      name.innerHTML = "Name : " + data[i].productList[j].name;
      let val = data[i].productList[j].name;
      //console.log(val);
      let add = document.createElement("button");
      add.setAttribute("id", "insert");
      add.innerHTML = "Add to the cart";
      add.onclick = function () {
        arr.push(val);
        console.log("Product Added to the cart");
        console.log("List of product present in cart array : " + arr);
      };

      let remove = document.createElement("button");
      remove.setAttribute("id", "erase");
      remove.innerHTML = "Remove from cart";
      remove.onclick = function () {
        removeItem(arr, val);
        console.log("Product removed from the cart");
        console.log("List of product present in cart array :  " + arr);
      };

      item.appendChild(name);

      let price = document.createElement("p");
      price.innerHTML = "Price : " + data[i].productList[j].price;
      item.appendChild(price);

      item.appendChild(add);
      item.appendChild(remove);

      itemCategory.appendChild(item);
    }
    app.appendChild(itemCategory);
  }
}
